import { jsPDF } from "jspdf"
import React, {Link} from 'react'


const PDFbutton = ({primaryColor, secondaryColor, adjectiveFirst, adjectiveSecond}) => {
	function composePDF(props) {
		void(0);
		const doc = new jsPDF({
		  orientation: "landscape",
		  unit: "in",
		  format: [4, 6]
		});


		doc.text(`Primary Color: ${primaryColor}`, 1, 1);
		doc.text(`Secondary Color: ${secondaryColor}`, 1, 1.4);
		doc.text(`First Adjective: ${adjectiveFirst}`, 1, 1.8);
		doc.text(`Second Adjective: ${adjectiveSecond}`, 1, 2.2);
		doc.save("roost-summary.pdf");

		// const pdf = "roost-summary.pdf";
		// window.open("roost-summary.pdf", "Roost-Summary");
		const link = document.createElement('a');
		link.href = `roost-summary.pdf`;
		document.body.appendChild(link);
		link.click();
		document.body.removeChild(link);
	}

	return (
		<button className="button"
		style={{
			marginTop: "2rem",
			marginBottom: "6rem",
			backgroundColor: "blue",
			color: "white",
			fontWeight: "bold",
			fontSize: "1.5rem",
		}}
		onClick={
			composePDF
			}
		>
			PDF it
		</button>
	)
}

export default PDFbutton
